<template>
  <div class="max-w-7xl mx-auto py-14 px-2 md:px-8">
    <div class="flex items-center mb-12">
      <i class="fa fa-file-code text-4xl text-primary mr-4"></i>
      <div>
        <h1 class="text-3xl font-bold text-primary mb-1">Base64 图片编码器</h1>
        <p class="text-neutral-500 dark:text-neutral-300 text-base">上传图片，快速生成 Base64 字符串，可选择格式并一键复制。</p>
      </div>
    </div>

    <div class="bg-white dark:bg-neutral-800 border-2 border-primary/20 dark:border-primary/30 rounded-2xl shadow-2xl p-8 md:p-14">
      <div class="flex flex-col md:flex-row gap-10 min-h-[340px]">
        <!-- 左侧：上传与图片信息 -->
        <div class="flex-1 min-w-[360px] border border-primary/10 dark:border-primary/20 rounded-xl bg-neutral-50 dark:bg-neutral-900 p-8 shadow-md flex flex-col justify-center">
          <div
            class="mb-8 border-2 border-dashed rounded-xl flex flex-col items-center justify-center py-12 bg-neutral-50 dark:bg-neutral-800 hover:border-primary transition-all relative"
            :class="{'border-primary': dragActive}"
            @dragover.prevent="dragActive = true"
            @dragleave.prevent="dragActive = false"
            @drop.prevent="onDrop"
          >
            <input type="file" accept="image/*" @change="onFileChange" class="absolute inset-0 opacity-0 cursor-pointer" />
            <div class="flex flex-col items-center pointer-events-none">
              <i class="fa fa-upload text-5xl text-primary mb-3"></i>
              <span class="text-lg text-neutral-600 dark:text-neutral-300">点击或拖拽图片到此处上传</span>
              <span class="text-sm text-neutral-400 mt-1">支持 PNG/JPG/JPEG/GIF/SVG 等格式</span>
            </div>
          </div>

          <div v-if="base64" class="mb-8">
            <div class="flex items-center mb-4">
              <img :src="base64DataUrl" alt="预览" class="w-36 h-36 object-contain border rounded shadow bg-white mr-6" />
              <div class="flex-1">
                <div class="flex flex-wrap items-center mb-2 gap-x-6 gap-y-2">
                  <span class="text-base text-neutral-500 dark:text-neutral-300">文件名：<b>{{ fileInfo.name }}</b></span>
                  <span class="text-base text-neutral-500 dark:text-neutral-300">类型：<b>{{ fileInfo.type }}</b></span>
                  <span class="text-base text-neutral-500 dark:text-neutral-300">大小：<b>{{ fileInfo.size }}</b></span>
                </div>
                <div class="flex items-center flex-wrap gap-3 mt-3">
                  <label class="text-sm text-neutral-500 dark:text-neutral-300">
                    <input type="radio" value="dataurl" v-model="format" class="mr-1" /> DataURL
                  </label>
                  <label class="text-sm text-neutral-500 dark:text-neutral-300">
                    <input type="radio" value="pure" v-model="format" class="mr-1" /> 纯Base64
                  </label>
                  <button @click="copyBase64" class="px-4 py-1.5 bg-primary text-white rounded hover:bg-secondary transition-colors text-sm">复制</button>
                  <button @click="clearAll" class="px-4 py-1.5 bg-neutral-200 dark:bg-neutral-700 text-neutral-700 dark:text-neutral-200 rounded hover:bg-neutral-300 dark:hover:bg-neutral-600 transition-colors text-sm">清除</button>
                  <span v-if="copied" class="text-green-500 text-sm ml-2">已复制！</span>
                </div>
              </div>
            </div>
          </div>

          <div v-else class="text-neutral-400 text-center mt-10">
            <i class="fa fa-image text-5xl mb-3"></i>
            <div class="text-lg">请上传图片以生成 Base64 编码</div>
          </div>
        </div>

        <!-- 右侧：Base64结果 -->
        <div class="flex-1 min-w-[360px] flex flex-col border border-primary/10 dark:border-primary/20 rounded-xl bg-neutral-50 dark:bg-neutral-900 p-8 shadow-md">
          <label class="mb-3 text-base text-neutral-600 dark:text-neutral-300 font-semibold flex items-center">
            <i class="fa fa-code mr-2"></i> Base64 结果
          </label>
          <textarea
            readonly
            class="w-full h-[260px] p-4 border rounded bg-neutral-50 dark:bg-neutral-900 text-sm font-mono select-all resize-none flex-1"
            :value="displayBase64"
            v-if="base64"
          ></textarea>
          <div v-else class="h-[260px] flex items-center justify-center border rounded bg-neutral-50 dark:bg-neutral-900 text-neutral-300 text-base">
            Base64 结果将在此处显示
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      base64: '',
      fileInfo: {},
      copied: false,
      dragActive: false,
      format: 'dataurl'
    };
  },
  computed: {
    base64DataUrl() {
      return this.base64 || '';
    },
    displayBase64() {
      if (!this.base64) return '';
      if (this.format === 'pure') {
        // 去掉 data:image/xxx;base64, 前缀
        return this.base64.replace(/^data:.*;base64,/, '');
      }
      return this.base64;
    }
  },
  methods: {
    onFileChange(e) {
      const file = e.target.files[0];
      if (!file) return;
      this.handleFile(file);
    },
    onDrop(e) {
      this.dragActive = false;
      const file = e.dataTransfer.files[0];
      if (file) this.handleFile(file);
    },
    handleFile(file) {
      if (!file.type.startsWith('image/')) {
        this.$toast && this.$toast.error('请选择图片文件');
        return;
      }
      const reader = new FileReader();
      reader.onload = (evt) => {
        this.base64 = evt.target.result;
        this.fileInfo = {
          name: file.name,
          type: file.type,
          size: this.formatSize(file.size)
        };
        this.copied = false;
      };
      reader.readAsDataURL(file);
    },
    copyBase64() {
      if (!this.displayBase64) return;
      navigator.clipboard.writeText(this.displayBase64).then(() => {
        this.copied = true;
        setTimeout(() => (this.copied = false), 1500);
      });
    },
    clearAll() {
      this.base64 = '';
      this.fileInfo = {};
      this.copied = false;
      this.format = 'dataurl';
    },
    formatSize(size) {
      if (size < 1024) return size + ' B';
      if (size < 1024 * 1024) return (size / 1024).toFixed(1) + ' KB';
      return (size / 1024 / 1024).toFixed(2) + ' MB';
    }
  }
};
</script>
